var template = require("./decisionTable.html.js");

var DirectiveController = [
  "$scope",
  function($scope) {
    $scope.control = {};
  }
];

var Directive = function() {
  return {
    restrict: "EAC",
    scope: {
      decisionTable: "=",
      control: "=?",
      table: "@",
      onLoad: "&"
    },
    controller: DirectiveController,
    template: template,

    link: function() {}
  };
};

module.exports = Directive;
