export default function pathUtil(id) {
  var uri = document.getElementsByTagName("base")[0].getAttribute(id);

  if (!id) {
    throw new Error("Uri base for " + id + " could not be resolved");
  }

  if (id === "engine") {
    return window.location.href.replace(/.*cockpit\/([^/]*).*/, "$1");
  }

  return uri;
}
