var template = require("./cam-inline-panel-property.html.js");

module.exports = function() {
  return {
    restrict: "A",
    template: template,
    scope: {
      property: "=camInlinePanelProperty",
      format: "&",
      onChange: "&",
      onRemove: "&",
      type: "@"
    },
    link: function($scope) {
      $scope.remove = function($event) {
        $event.preventDefault();
        $event.stopPropagation();

        $scope.onRemove();
      };
    }
  };
};
