import { getLocale } from "utils/config";

export default function translate(translateString, placeholder) {
  const locale = getLocale().labels;

  const translated = locale[translateString];
  placeholder = placeholder || {};

  if (!translated) {
    return translateString;
  }

  return injectData(translated, placeholder);
}

function injectData(template, data) {
  return template.replace(/\{\{([\w.]*)\}\}/g, (str, key) => {
    return data[key] || str;
  });
}
