import React from "react";
import classNames from "classnames";

import "./Tooltip.scss";

export default function Tooltip({ title, children, position }) {
  return (
    <div className={classNames("Tooltip", position ? position : "right")}>
      <div className="title">{title}</div>
      <div className="text">{children}</div>
    </div>
  );
}
