var template = require("./cam-share-link.html.js");

module.exports = [
  "$location",
  function($location) {
    return {
      restrict: "A",
      template: template,
      link: function($scope) {
        $scope.getLink = $location.absUrl.bind($location);
      }
    };
  }
];
