var template = require("./cam-searchable-area.html.js");

module.exports = function() {
  return {
    restrict: "A",
    transclude: true,
    template: template,
    controller: "CamPaginationSearchIntegrationController as Searchable",
    scope: {
      config: "=",
      loadingState: "=",
      loadingError: "@",
      arrayTypes: "=?",
      variableTypes: "=?",
      buildCustomQuery: "&",
      onSearchChange: "&",
      textEmpty: "@",
      storageGroup: "=",
      searchId: "@?",
      blocked: "="
    }
  };
};
