import module from "../legacy/client/scripts/pages/processDefinition";
import angular from "angular";

export default function() {
  var ngModule = angular.module("cam.cockpit.runtime.processDefinition", [
    module.name
  ]);

  const node = document.createElement("div");
  node.innerHTML = `
  <div
      cam-breadcrumbs-panel
      divider="&raquo;"
      ng-cloak
      class="breadcrumbs-panel"></div>
  <div process-definition-page></div>
  `;
  node.className = "ctn-main";

  return { node, module: ngModule };
}
